
import classNames from 'classnames';
import { useState } from 'react';
import styles from './index.less'
// 样式display：inline-flex label 不需要forHtml 同一个样式 a.b 


const Radio = (props: any) => {
    const { selectedKey, onSelect,type, options } = props;
    return (
        <div className={classNames({
            [styles.vertical]: type === 'vertical'
        })}>
            {
                options.map((item:any) => {
                    return (
                        <label className={classNames(styles.container, {
                            [styles.active]: item.key === selectedKey,
                            [styles.disable]: item.disabled
                        })
                        }
                        key={item.key}
                        >
                            <input
                                className={styles.inputRadio}
                                type="radio"
                                disabled={item.disabled}
                                defaultChecked={item.key === selectedKey}
                                onClick={() => {
                                    if(item.key !== selectedKey) {
                                        onSelect(item.key)
                                    }
                                }} />
                            <span className={styles.circle}></span>
                            <span>{item.content}</span>
                        </label>
                    );
                })
            }
        </div>
    )
}

export default Radio;