import styles from './index.less'
import Radio from 'component/Radio'
import { useState } from 'react'
const options = [
    { key: 1, content: '第一个选项', className: styles.radio },
    { key: 2, content: '第二个选项', className: styles.radio },
    {
        key: 3, content: '禁用的选项', disabled: true, className: styles.radio,
    },
]
const RadioVerticalDemo = () => {
    const [key,setKey] = useState(3);
    return (
        <div>
            <Radio 
            options={options}
            selectedKey={key}
            type="vertical"
            onSelect={(selectedKey:number) => setKey(selectedKey)}
            />
        </div>
    )
}

export default RadioVerticalDemo